import PostService from '../services/PostService';
import { loading, doneLoading } from './SystemActions';

// THUNK
export function loadPosts() {
    return async dispatch => {
        try {
            dispatch(loading());
            const posts = await PostService.getPosts();
            dispatch(_setPosts(posts));
        } catch (err) {
            console.log('UserActions: err in loadPosts', err);
        } finally {
            dispatch(doneLoading());
        }
    };
}
// THUNK
export function loadPost(postId) {
    return async dispatch => {
        try {
            dispatch(loading());
            const post = await PostService.getPost(postId);
            dispatch(_setPost(post));
            return post;
        } catch (err) {
            console.log('UserActions: err in loadPost', err);
        } finally {
            dispatch(doneLoading());
        }
    };
}
export function addPost(postObj) {
    return async dispatch => {
        try {
            dispatch(loading());
            const addedPost = await PostService.add(postObj);
            dispatch(_addPost(addedPost));
        } catch (err) {
            console.log('PostActions: err in addPost', err);
        } finally {
            dispatch(doneLoading());
        }
    };
}
export function removePost(postId) {
    console.log(postId)
    return async dispatch => {
        try {
            dispatch(loading());
            await PostService.remove(postId);
            dispatch(_removePost(postId));
        } catch (err) {
            console.log('PostActions: err in removePost', err);
        } finally {
            dispatch(doneLoading());
        }
    };
}
export function likeUnkikePost(post, likedByObj, isLiked) {
    return async dispatch => {
        try {
            dispatch(loading());
            var p = null;
            (!isLiked) ? p = await PostService.likePost(likedByObj, post)
                : p = await PostService.unlikePost(likedByObj, post);

            const posts = await PostService.getPosts();
            dispatch(_setPosts(posts));

            return p;
        } catch (err) {
            console.log('PostActions: err in likePost/unlike', err);
        } finally {
            dispatch(doneLoading());
        }
    };

}

function _setPosts(posts) {
    return {
        type: 'SET_POSTS',
        posts: posts
    };
}
function _setPost(post) {
    return {
        type: 'SET_POST',
        selectedPost: post
    };
}
function _addPost(post) {
    return {
        type: 'POST_ADD',
        post
    };
}
function _removePost(postId) {
    return {
        type: 'POST_REMOVE',
        postId
    };
}
