import HttpService from './HttpService';


export default {
  add,
  query,
  remove
};

function query(postId) {
  try {
    const comments = HttpService.get(`post/${postId}/comments`,
      { postId }
    );
    return comments;
  }
  catch (err) {
    console.log(err)
  }
}
function remove(commentId) {
  return HttpService.delete(`comment/${commentId}`);
}



async function add(commentObj, postId) {
  try {
    const addedComment = await HttpService.post(`post/${postId}/comments`,
      { commentObj, postId }
    );
    return addedComment
  } catch (err) {
    console.log(err)
  }
}
