import React, { Component } from 'react';
import '../assets/css/Comments/Comments.css'

export default class Comments extends Component {
    state = { txt: "" };
    inputChange = ev => {
        let fieldName = ev.target.name;
        this.setState({ [fieldName]: ev.target.value });
    };

    onAddMessage = async (ev) => {
        ev.preventDefault();
        if (!this.state.txt) return
        this.props.sendComment(this.state.txt)
        this.setState({ txt: '' })
    }

    render() {
        const { comments } = this.props;
        return (<div className="b-post-comments flex col">
            <ul className="b-post-comments-list"> {comments.map((comment) => (
                <li className="b-post-comment-item-container flex row" key={comment._id} >

                    <div className="b-post-comment-user-img-container">
                        <img className="b-post-comment-user-img" alt="commenter pic" src={comment.owner.imgUrl}></img>
                    </div>
                    <div className="b-post-comment-txt">
                        <span className="b-post-comment-user-nickname">{comment.owner.username}</span>
                        {comment.txt}
                    </div>
                </li>
            ))}
            </ul>
        </div>
        );
    }
}
