import React, { Component } from 'react';
import { connect } from 'react-redux';
import SocketService from '../services/SocketService';
import Comments from '../cmps/Comments';
import TextInput from '../cmps/TextInput';
import { addComment, loadComments } from '../actions/CommentActions';
import { loadPosts, likeUnkikePost, addPost, removePost, loadPost } from '../actions/PostActions.js';
import PostOptionsModal from '../cmps/PostOptionsModal'
import ScrollToTop from '../cmps/ScrollToTop'
import UserService from '../services/UserService';
// import Like from '../cmps/Like'
import '../assets/css/PostPage/PostPage.css'
import utils from '../utils'
const blank = require('../assets/img/like-blank.png')
const liked = require('../assets/img/like-active.png')
class PostPage extends Component {
    _isMounted = false;
    _isUpdate = false
    state = {
        comments: [],
        createdAt: '',
        user: this.props.location.state.user,
        post: this.props.location.state.post,
        likes: '',
        isLiked: false,
        likeImgSrc: blank,
        whoLiked: ''
    }
    async componentDidMount() {
        this._isMounted = true;
        if (this._isMounted && !this._isUpdate) {
            const { post, user } = this.props.location.state;
            this.props.loadPost(post._id)
                .then((p) => {
                    this.checkLikeState(p, user)
                    this.countLikes(p)
                })
            this.getTimeElapsed(post.createdAt)
            if (this.state.comments)
                this.props.loadComments(post._id)
                    .then((comments) => {
                        this.setState({
                            comments
                        })
                    })
            SocketService.setup();
            SocketService.emit('post', post._id);
            SocketService.on('post addComment', this.addComment);
        }
    }
    async componentDidUpdate(prevProps, prevState) {
        this._isMounted = false;
        if (JSON.stringify(prevState.post.likedBy) !== JSON.stringify(this.state.post.likedBy)) {

            this.props.loadPost(this.state.post._id)
                .then((p) => {
                    this.checkLikeState(p, this.state.user)
                    this.countLikes(p)
                })
        }
    }
    async componentWillUnmount() {
        this._isMounted = false;
        SocketService.off('post addComment', this.addComment);
        SocketService.terminate();
    }

    getTimeElapsed = async (createdAt) => {
        var time = await utils.getTimeElapsed(createdAt)
        this.setState({ createdAt: time })
    }
    addComment = (newComment) => {
        this.setState(prevState => ({ comments: [...prevState.comments, newComment] }));
    };
    sendComment = async (txt) => {
        let postId = this.props.location.state.post._id

        var date = new Date();
        var timestamp = date.getTime();

        var owner = (this.props.loggedInUser) ? this.props.loggedInUser
            : await UserService.getById('5e5796c01c9d44000049191b')

        var commentObj = {
            _id: '',
            createdAt: timestamp,
            txt: txt,
            owner: owner
        }
        const newComment = await this.props.addComment(commentObj, postId)//mongoDB adds ObjectId for key in map
        SocketService.emit('post newComment', newComment);
    };

    postOptionsClicked = (postId) => {
        var bg = document.querySelector(`.options-modal-${postId}, options-modal-bg`)
        var modal = document.querySelector(`.post-options-modal-${postId}, post-options-modal`)

        if (!this.state.isOptsModalOpen) {
            bg.classList.remove('hidden')
            modal.classList.remove('hidden')
            modal.classList.add('scale-in-hor-center')
            modal.classList.remove('scale-out-horizontal')
            this.setState({ isOptsModalOpen: true })
        } else {
            modal.classList.add('scale-out-horizontal')
            modal.classList.remove('scale-in-hor-center')
            setTimeout(() => {
                modal.classList.add('hidden')
                bg.classList.add('hidden')
            }, 300);
            this.setState({ isOptsModalOpen: false })
        }
    }
    onRemovePost = async (postId) => {
        await this.props.removePost(postId)
        this.props.history.push({
            pathname: `/`
        });
    }

    checkLikeState = (post, user) => {
        var isNotFound = true;
        post.likedBy.forEach(liker => {
            if (liker._id === user._id) {
                isNotFound = false;
                this.setState({ isLiked: true, likeImgSrc: liked, user, post })
            }
        });
        if (isNotFound) {
            console.log('checkLikeState() liker not found - heart should blank')
            this.setState({ isLiked: false, likeImgSrc: blank, user, post })
        }
    }
    onLikeClicked = async (post, user, isLiked) => {
        var isDone = false
        if (!isLiked) {//like post
            this.setState({ isLiked: true, likeImgSrc: liked })
        } else if (isLiked) {//unlike post
            this.setState({ isLiked: false, likeImgSrc: blank })
        }

        try {
            await this.props.likeUnkikePost(post, user, isLiked)
                .then(() => { isDone = true })
        } catch (err) {
            console.log('error in likeUnkikePost: ', err)
        }

        if (isDone)
            this.props.loadPost(post._id)
                .then((resPost) => { this.setState({ post: resPost }) })
    }
    seeWhoLiked = (post, e) => {
        let elSpan = e.target
        let whoLiked = ''
        post.likedBy.forEach((user, i) => {
            if (i !== post.likedBy.length - 1 && i < 10) {
                whoLiked += user.username + ', \n'
            }
            else {
                whoLiked += user.username + ' '
                return;
            }
        });
        elSpan.lable = whoLiked;
        this.setState({ whoLiked: whoLiked })
    }
    countLikes = (post) => {
        let likesCount = post.likedBy.length
        let result = ''
        if (likesCount > 1) {
            result = likesCount + ' likes';
        } else if (likesCount === 0) {
            result = ''
        } else {
            result = '1 like';
        }
        this.setState({ likes: result })
    }
    render() {
        const { comments, user, post, isLiked, likeImgSrc, likes } = this.state
        const { loggedInUser } = this.props
        return (
            <div className="post-page-container flex">

                <img className="post-page-img" src={post.imgUrl} alt="post pic"></img>
                <div className="post-page-body">

                    <div className="post-page-header-inner-container flex col">
                        <div className="post-page-header flex row">
                            <div>
                                <img className="post-page-header-owner-img" alt="post owner pic" src={post.owner.imgUrl}></img>
                                <span className="post-page-header-owner-nickname">{post.owner.username}</span>
                            </div>
                            <img className="post-page-header-options-btn" onClick={() => this.postOptionsClicked(post._id)}
                                src={require('../assets/img/options.png')} alt="options pic" title="options"></img>
                        </div>
                    </div>
                    <section className="post-page-comments-container">
                        <div className="post-page-comments-wrapper">
                            <Comments comments={comments} loggedInUser={loggedInUser} />
                        </div>
                        <div className="post-page-buttons-container flex row">
                            <img className={'post-page-btn'} alt="like" title="like"
                                onClick={() => this.onLikeClicked(post, user, isLiked)} src={likeImgSrc}></img>
                            {/* <img className={'post-page-btn'} alt="share" title="share" onClick={() => shareClicked(post._id)} src={require('../assets/img/share.png')}></img> */}
                        </div>
                        <div className="post-page-buttons-data-container  flex row">
                            {post.likedBy &&
                                <span className="post-page-show-likes" title={this.state.whoLiked} onMouseOver={(e) => this.seeWhoLiked(post, e)}>{likes}</span>}
                        </div>
                        <div className="post-page-buttons-data-container  flex row">
                            <span className="post-page-create-date" >{this.state.createdAt}</span>
                        </div>
                        <TextInput user={user} sendComment={this.sendComment} />
                    </section>



                </div>
                <PostOptionsModal postId={post._id}>
                    <div className={`post-options-modal-${post._id} post-options-modal flex col`} >
                        <img className={`close-modal-img-btn`} onClick={() => this.postOptionsClicked(post._id)} src="https://res.cloudinary.com/ilya432/image/upload/v1581638014/close_exlrw7.png" alt="close modal" title="close"></img>

                        {loggedInUser && (post.owner._id === loggedInUser._id) && <span className={`post-opt`} postid={post._id} onClick={() => this.onRemovePost(post._id)}>delete</span>}
                        <span className={`post-opt`} postid={post._id}>share</span>
                    </div>
                </PostOptionsModal>
                <ScrollToTop className="scroll-to-top" />
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        loggedInUser: state.user.loggedInUser
    };
};
const mapDispatchToProps = {
    addComment,
    loadComments,
    loadPost, loadPosts, likeUnkikePost, addPost, removePost
};
export default connect(mapStateToProps, mapDispatchToProps)(PostPage);


