import React from 'react';

import FriendsFilter from '../cmps/FriendsFilter/FriendsFilter'
import '../assets/css/search/search.css'
import FriendList from '../cmps/FriendList'

// React/Redux
import { connect } from 'react-redux';
import { loadUsers } from '../actions/UserActions.js';
import { loadPosts } from '../actions/PostActions.js';
import { loadFriends, loadSelectedFriend } from '../actions/FriendActions.js';

class Search extends React.Component {
    state = {
        selectedFriend: ''
    }
    componentDidMount() {
        this.props.loadUsers('search')
        this.props.loadPosts()
    }
    filterFriends = (filterBy) => {
        (filterBy) ? this.props.loadFriends(this.props.users, filterBy)
            : this.props.loadFriends();

        this.handleDropDown()
    }
    handleDropDown = () => {
        if (this.props.friends) {
            document.querySelector('.friends-container').classList.remove('hidden')
        } else {
            document.querySelector('.friends-container').classList.add('hidden')
        }
    }

    onSelectFriend = async (friend) => {
        const _id = friend._id
        this.props.history.push({
            pathname: `/user/${_id}`,
            state: {
                posts: this.props.posts,
                users: this.props.users
            }
        });
    }
    render() {
        const { friends, users } = this.props
        return <div className="search-container">
            <FriendsFilter users={users} filterFriends={this.filterFriends}></FriendsFilter>
            <div className="friends-container hidden">
                <div className="friends-dropdown-div">
                    <FriendList friends={friends} onSelectFriend={this.onSelectFriend} /></div>
            </div>
        </div>
    }
}

const mapStateToProps = state => {
    return {
        posts: state.post.posts,
        users: state.user.users,
        friends: state.friend.friends,
        selectedFriend: state.friend.selectedFriend,
        // loggedInUser: state.user.loggedInUser,
    };
};
const mapDispatchToProps = {
    loadUsers,
    loadFriends,
    loadSelectedFriend,
    loadPosts
};

export default connect(mapStateToProps, mapDispatchToProps)(Search);