// React/Redux
import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { loadUsers } from '../actions/UserActions.js';
import { loadPosts, likeUnkikePost, addPost, removePost } from '../actions/PostActions.js';
import { addComment } from '../actions/CommentActions';
// import UserService from '../services/UserService';
import ScrollToTop from '../cmps/ScrollToTop'
// Style:
import '../../src/assets/css/index/index.css';
import '../../src/assets/css/Home/Home.css';
import '../../src/assets/css/Nav/Nav.css';

// Components
import Feed from '../cmps/Feed'
import PostAdd from '../cmps/PostAdd'
// import _ from 'underscore'
var randomUser = require('../assets/img/users/randomUser.png');

let lastScrollTop = 0;
let scrollDownCount = 0;
let scrollUpCount = 0;
let isAppeared = false;
class Home extends Component {
  _isMounted = false;

  constructor(props) {
    super(props);
    const guest = { _id: '5e5796c01c9d44000049191b', username: 'guest', imgUrl: randomUser }
    this.state = {
      user: (this.props.loggedInUser) ? this.props.loggedInUser : guest
    }
  }

  async componentDidMount() {
    this._isMounted = true;
    if (this._isMounted) {
      window.addEventListener("scroll", this.toggleNav)
      // this.props.loadUsers('home');
      // console.log('users home: ' + JSON.stringify(this.props.users))
      this.props.loadPosts();
    }
  }
  async componentWillUnmount() {
    this._isMounted = false;
    window.removeEventListener('scroll', this.toggleNav());
  }

  toggleNav() {
    var nav = document.querySelector('.nav')
    var st = window.pageYOffset || document.documentElement.scrollTop;
    // downscroll code
    if (st > lastScrollTop) {
      scrollDownCount++;
      scrollUpCount = 0
      if (window.pageYOffset >= 100) {//turn opaque
        nav.classList.add('nav-opaque');
      }
      if (window.pageYOffset > 700) {//hide 1st time
        isAppeared = true
        nav.classList.add('nav-folded');
      }
      if (nav !== null && isAppeared === true && window.pageYOffset >= 700) {//next hides
        nav.classList.add('nav-folded');
      }
      if (window.pageYOffset <= 700 && isAppeared === true) {//reveal
        if (nav !== null) {
          isAppeared = false
          nav.classList.remove('nav-folded');
        }
      }
    }
    // upscroll code
    else {
      scrollUpCount++;
      scrollDownCount = 0;
      if (window.pageYOffset <= 100) {//remove opacity - offsetY between 0px-100px
        nav.classList.remove('nav-opaque');
      }
      if (scrollUpCount === 10) {//appear nav (already set to opaque)
        nav.classList.remove('nav-folded');
      }
      if (scrollUpCount === 20) {//remove opacity - show full Nav
        nav.classList.remove('nav-opaque');
      }
    }
    lastScrollTop = st <= 0 ? 0 : st; // For Mobile or negative scrolling
  }

  render() {
    const { loggedInUser, history, users, posts, comments, removePost, addComment, likeUnkikePost } = this.props
    const { user } = this.state
    return (
      <div className="home">
        <div className="home-container">
          <Feed loggedInUser={loggedInUser} user={user} users={users} posts={posts} history={history} comments={comments} removePost={removePost} addComment={addComment} likeUnkikePost={likeUnkikePost} />
          {this.props.users &&
            <section className="extras-container">
              <div className="user-image-container">
                {loggedInUser && <Link to={location => ({
                  ...location,
                  pathname: `/user/logged/${user._id}`,
                  state: {
                    posts: posts,
                    users: users
                  }
                })}>
                  <img className="user-image" alt="user page" src={loggedInUser.imgUrl}></img>
                </Link>}
                {!loggedInUser && <img className="user-image" alt="user page" src={user.imgUrl}></img>}
                <span className="loggedin-user-nickname">{user.username}</span>
              </div>
              <ScrollToTop className="scroll-to-top" />
              <div className="extras">
                <PostAdd addPost={this.props.addPost} loggedInUser={loggedInUser} user={user} props={this.props} />
              </div>
            </section>}
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    users: state.user.users,
    loggedInUser: state.user.loggedInUser,
    posts: state.post.posts
  };
};
const mapDispatchToProps = {
  loadUsers,
  loadPosts, likeUnkikePost, addPost, removePost,
  addComment
};
export default connect(mapStateToProps, mapDispatchToProps)(Home);
