// 'use strict'

export default { getCurrentTime, getTimeElapsed, getAnalitics, addUp };

function getAnalitics() {
    console.log("Cookies: " + navigator.cookieEnabled);
    console.log("Browser Language: " + navigator.browserLanguage);//undefined
    console.log("Language: " + navigator.language);
    console.log("Platform: " + navigator.platform);
    // console.log("keyboard: " + navigator.keyboard);
    // console.log("Connection type: " + navigator.netInfo.type);//
    console.log("Connection saveData: " + navigator.connection.saveData);
    console.log("Connection obj: " + navigator.connection.effectiveType);//
    console.log("Connection Speed: " + navigator.connectionSpeed);//undefined
    console.log("User Agent: " + navigator.userAgent);
    console.log("Webdriver: " + navigator.webdriver);
    console.log("Geolocation: " + navigator.geolocation);
}
function getCurrentTime(timestamp) {
    var newDate = new Date(timestamp);
    var date = newDate.getDate();
    var month = newDate.getMonth();
    var year = newDate.getFullYear();
    var hours = newDate.getHours();
    var minutes = (newDate.getMinutes() < 10 ? '0' : '') + newDate.getMinutes();// if less than 10 add '0'
    var seconds = (newDate.getSeconds() < 10 ? '0' : '') + newDate.getSeconds();
    var monthDateYear = date + "/" + (month + 1) + "/" + year + " - " + hours + ":" + minutes + ":" + seconds;
    return monthDateYear;
}
function getTimeElapsed(timestamp) {
    const year = 29030400, month = 2419200, week = 604800, day = 86400, hour = 3600, minute = 60;

    var now = Date.now()
    var delta = Math.abs(now - timestamp) / 1000;//total seconds between the times    

    var years = Math.floor(delta / year);
    delta -= years * year;
    var months = Math.floor(delta / month) % 12;
    delta -= months * month;
    var weeks = Math.floor(delta / week) % 4; delta -= weeks * week;
    var days = Math.floor(delta / day) % 7; delta -= days * day;
    var hours = Math.floor(delta / hour) % 24; delta -= hours * hour;
    var minutes = Math.floor(delta / minute) % 60; delta -= minutes * minute;
    var seconds = Math.floor(delta % minute);

    var result = ''

    switch (true) {
        case (years > 0):
            result = `${years} Y ${months} M`// Y - M
            break;
        case (years === 0 && months > 0 && weeks === 0 && days === 0):// M
            result = `${months} M`
            break;
        case (years === 0 && months > 0 && weeks > 0):// M - w
            result = `${months} M  ${weeks} w`
            break;
        case (years === 0 && months > 0 && weeks === 0 && days > 0):// M - d
            result = `${months} M  ${days} d`
            break;
        case (years === 0 && months === 0 && weeks > 0 && days === 0):// w
            result = `${weeks} w`
            break;
        case (years === 0 && months === 0 && weeks > 0 && days > 0):// w - d
            result = `${weeks} w  ${days} d`
            break;
        case (years === 0 && months === 0 && weeks === 0 && days > 0 && hours === 0):// d
            result = `${days} d`
            break;
        case (years === 0 && months === 0 && weeks === 0 && days > 0 && hours > 0):// d - h
            result = `${days} d  ${hours} h`
            break;
        case (years === 0 && months === 0 && weeks === 0 && days === 0 && hours > 0 && minutes === 0):// h
            result = `${hours} h`
            break;
        case (years === 0 && months === 0 && weeks === 0 && days === 0 && hours > 0 && minutes > 0):// h - m
            result = `${hours} h  ${minutes} m`
            break;
        case (years === 0 && months === 0 && weeks === 0 && days === 0 && hours === 0 && minutes > 0):// m
            result = `${minutes} m`
            break;
        case (years === 0 && months === 0 && weeks === 0 && days === 0 && hours === 0 && minutes === 1 && seconds > 0):// m - s
            result = `${minutes} m - ${seconds} s`
            break;
        case (years === 0 && months === 0 && weeks === 0 && days === 0 && hours === 0 && minutes === 0 && seconds > 0):// s
            result = `${seconds} s`
            break;
        default:
            break;
    }
    return result
}







//Recursion
function addUp(num) {
    return (num * (num + 1)) / 2;
}
