import React from 'react';
import '../assets/css/Feed/feed.css'
import Post from '../cmps/Post'
import PostOptionsModal from '../cmps/PostOptionsModal'
import ShareModal from '../cmps/ShareModal'
import '../assets/css/modals/PostOptionsModal/PostOptionsModal.css'
import '../assets/css/modals/ShareModal/ShareModal.css'

export default class Feed extends React.Component {
    state = {
        isOptsModalOpen: false,
        isShareModalOpen: false,
        postId: ''
    }
    componentDidMount() {
    }
    // post header:
    toggleModal = (postId) => {
        var bg = document.querySelector(`.options-modal-${postId}, options-modal-bg`)
        var modal = document.querySelector(`.post-options-modal-${postId}, post-options-modal`)

        if (!this.state.isOptsModalOpen) {//close
            bg.classList.remove('hidden')
            modal.classList.remove('hidden', 'scale-out-horizontal')
            modal.classList.add('scale-in-hor-center')
            this.setState({ isOptsModalOpen: true })
        } else {//open
            modal.classList.add('scale-out-horizontal')
            modal.classList.remove('scale-in-hor-center')
            setTimeout(() => {
                modal.classList.add('hidden')
                bg.classList.add('hidden')
            }, 300);
            this.setState({ isOptsModalOpen: false })
        }
    }
    toPost = (postId, post) => {
        this.props.history.push({
            pathname: `/post/${postId}/comments`,
            state: {
                post: post
            }
        });
    }
    shareClicked = (postId) => {

        var bg = document.querySelector(`.share-modal-${postId}, share-modal-bg`)
        var modal = document.querySelector(`.post-share-modal-${postId}, post-share-modal`)

        if (!this.state.isShareModalOpen) {
            bg.classList.remove('hidden')

            modal.classList.remove('hidden')
            modal.classList.add('scale-in-hor-center')
            modal.classList.remove('scale-out-horizontal')
            this.setState({ isShareModalOpen: true })
        } else {
            // bg.style.display = 'hidden'
            modal.classList.add('scale-out-horizontal')
            modal.classList.remove('scale-in-hor-center')
            setTimeout(() => {
                modal.classList.add('hidden')
                bg.classList.add('hidden')
            }, 300);
            this.setState({ isShareModalOpen: false })
        }
    }
    onRemovePost = (postId) => {
        console.log('onRemovePost postId: ' + postId)
        this.props.removePost(postId)
        this.toggleModal(postId)
    }

    render() {
        const { history, user, users, posts, loggedInUser, handleChange, likeUnkikePost, addComment } = this.props;
        return <div className="feed-container">
            {posts && <ul className="posts-list flex col-rev">
                {posts.map((post, i) => (
                    <li key={post._id}>
                        <Post history={history} user={user} post={post} users={users} posts={posts}
                            handleChange={handleChange}
                            likeUnkikePost={likeUnkikePost} toggleModal={this.toggleModal} shareClicked={this.shareClicked} addComment={addComment}>
                        </Post>
                        <PostOptionsModal postId={post._id} toggleModal={this.toggleModal}>
                            <div className={`post-options-modal-${post._id} post-options-modal flex col`} >
                                <img className={`close-modal-img-btn`} onClick={() => this.toggleModal(post._id)} src="https://res.cloudinary.com/ilya432/image/upload/v1581638014/close_exlrw7.png" alt="close modal" title="close"></img>
                                {loggedInUser && (post.owner._id === loggedInUser._id) && <span className={`post-opt`} postid={post._id} onClick={() => this.onRemovePost(post._id)}>delete</span>}
                                <span className={`post-opt`} postid={post._id}>share</span>
                                {loggedInUser && <span className={`post-opt`} postid={post._id} onClick={() => this.toPost(post._id, post)}>comment</span>}
                            </div>
                        </PostOptionsModal>
                        <ShareModal postId={post._id}>
                            <div className={`post-share-modal-${post._id} post-share-modal flex col`} >
                                <img className={`close-modal-img-btn`} onClick={() => this.shareClicked(post._id)} src="https://res.cloudinary.com/ilya432/image/upload/v1581638014/close_exlrw7.png" alt="close modal" title="close"></img>
                                <span className={`share-opt`} postid={post._id}>facebook</span>
                            </div>
                        </ShareModal>
                    </li>
                ))}
            </ul>}
        </div>
    }
}