const BASE_URL = process.env.NODE_ENV === 'production'
    ? '/api/'
    : '//localhost:3030/api/'

async function uploadImage(imgUrl) {
    return fetch(`${BASE_URL}upload`, {
        method: 'POST',
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ imgUrl })
    })
        .then(res => res.json())
        .then(res => { return res })
        .catch(err => console.error(err))
}

export default {
    uploadImage
}

