import CommentService from '../services/CommentService';

export function loadComments(postId) {
  return async dispatch => {
    try {
      const comments = await CommentService.query(postId);
      dispatch(setComments(comments));
      return comments;
    } catch (err) {
      console.log('CommentActions: err in loadComments', err);
    }
  };
}

export function addComment(comment, postId) {
  return async dispatch => {
    try {
      const addedComment = await CommentService.add(comment, postId);
      dispatch(_addComment(addedComment));
      return addedComment;
    } catch (err) {
      console.log('CommentActions: err in addComment', err);
    }
  };
}


function setComments(comments) {
  return {
    type: 'SET_COMMENTS',
    comments
  };
}
function _addComment(comment) {
  return {
    type: 'COMMENT_ADD',
    comment
  };
}
