import React from 'react';
import loadingGif from './../assets/img/loading.gif'
import closeImgBtn from './../assets/img/close2.png'

import '../assets/css/PostAdd/postAdd.css'
import UploadService from "../services/UploadService";
const blankUpldImg = require('../assets/img/blankUpldImg.png')

export default class PostAdd extends React.Component {
    state = {
        selectedFile: null,
        postImgUrl: null,
        imagePreviewUrl: null,
        postTxt: '',
        postTagsText: '',
        tags: []
    }
    onUpload = () => {
        const uploadButton = document.querySelector('.upload-hidden-input')
        uploadButton.click()//will open file explorer
    }
    onImageSelected = async (event) => {
        const addPostBtn = document.querySelector('.add-post-btn'); addPostBtn.classList.add('hidden')
        if (event.target.files[0]) {
            const loadingImg = document.querySelector('.loading-gif')
            loadingImg.style['display'] = 'initial';//show loading

            const form = document.querySelector('.post-add-form')
            form.style['display'] = 'initial'//show form
            this.setState({ selectedFile: event.target.files[0] })
            var file = event.target.files[0]

            let reader = new FileReader();
            reader.onloadend = async () => {
                this.setState({
                    imagePreviewUrl: reader.result
                });
                //Show uploaded image
                const img = document.querySelector('.post-preview-img')
                img.src = this.state.imagePreviewUrl
                img.style['display'] = 'initial'
                //hide loading
                const loading = document.querySelector('.loading-gif')
                loading.classList.add('hidden');
                loading.style['display'] = 'none'

                await UploadService.uploadImage(this.state.imagePreviewUrl)
                    .then(res => {
                        // console.log('postPage res: ' + JSON.stringify(res.secure_url))
                        this.setState({ postImgUrl: res.secure_url })
                    })
            }
            reader.readAsDataURL(file)
        }
        else {
            console.log('No image was loaded')
        }

    }
    onAddPost = async (e) => {
        e.preventDefault()
        const addPostBtn = document.querySelector('.add-post-btn')
        addPostBtn.classList.remove('hidden')

        var date = new Date();
        var timestamp = date.getTime();
        var tagsTxt = this.state.postTagsText
        const tags = tagsTxt.toLowerCase().split(",").filter(function (tag) {
            return tag !== '';
        });

        var owner = (this.props.loggedInUser) ? this.props.loggedInUser
            : this.props.user

        var postObj = {
            _id: '4',
            imgUrl: this.state.postImgUrl,
            createdAt: timestamp,
            owner: owner,
            txt: this.state.postTxt,
            tags: tags,
            likedBy: [],
            comments: []
        }
        this.finallySubmit(postObj);
    }
    finallySubmit = postObj => {
        this.props.addPost(postObj)
        // const box = document.querySelector('.post-add-form'); box.style['display'] = 'none';
        this.cancleUpload()
    }
    changePostTxtInput = (ev) => {
        this.setState({ postTxt: ev.target.value })
    }
    changePostTagsInput = (ev) => {
        this.setState({ postTagsText: ev.target.value.trim() })
    }
    cancleUpload = () => {
        const addPostBtn = document.querySelector('.add-post-btn'); addPostBtn.classList.remove('hidden')
        const previewImg = document.querySelector('.post-preview-img'); previewImg.style['display'] = 'none';
        this.setState({ postImgUrl: blankUpldImg })
        const form = document.querySelector('.post-add-form')
        form.style['display'] = 'none'
        this.normalizeFields()
    }
    normalizeFields = () => {
        const uploadButton = document.querySelector('.upload-hidden-input')
        uploadButton.value = ''
        this.setState({ selectedFile: null })
        this.setState({ postImgUrl: null })
        this.setState({ imagePreviewUrl: null })
        this.setState({ postTxt: '' })
        this.setState({ postTagsText: '' })
        this.setState({ tags: [] })
    }

    render() {
        return <div className="PostAdd">
            <input className="upload-hidden-input" type="file" onChange={this.onImageSelected}></input>
            <button className="add-post-btn" onClick={this.onUpload} title="add post" >Add post</button>
            <form className="post-add-form" onSubmit={this.onAddPost}>
                <button className="upload-hidden-img-button" onClick={this.onUploadImage} >Upload image</button>
                <div className="loading-container">
                    <img className="close-img-btn" alt="close" title="close" onClick={this.cancleUpload} src={closeImgBtn}></img>
                    <img className="post-preview-img" onClick={this.onUpload} src={this.state.imagePreviewUrl} alt="post pic" title="change pic"></img>
                    <img className="loading-gif" alt="load gif" src={loadingGif}></img>
                </div>
                <div className="add-post-container flex col">
                    <label className="add-post-lbl">Say something...</label>
                    <input className="add-post-txt" type="text" onChange={this.changePostTxtInput} value={this.state.postTxt}></input>

                    <label className="add-post-lbl">Tags(use ',')</label>
                    <div className="tags-container flex row">
                        <input className="add-post-tags" type="text" onChange={this.changePostTagsInput} value={this.state.postTagsText}></input>
                        <button className="post-preview-submit-btn">Submit</button>
                    </div>
                </div>
            </form>
        </div>
    }
}