import React, { Component } from 'react';
import { connect } from 'react-redux';
import '../assets/css/UserPage/userPage.css'
import { loadPosts } from '../actions/PostActions';
import { loadUsers } from '../actions/UserActions';
import UserService from '.././services/UserService';
// import { loadFriends, loadSelectedFriend } from '../actions/FriendActions.js';

class UserPage extends Component {

    state = {
        postsCount: 0,
        followersCount: 0,
        followingCount: 0,
        posts: [],
        users: [],
        user: {},
        // friends: []
    }
    _isMounted = false;
    _isUpdated = false;

    componentDidMount = () => {
        this.props.loadPosts()
            .then((posts) => {
                if (!this._isMounted) {
                    this.setState({ posts })
                }
                this.props.loadUsers('UserPage')
                    .then((users) => {
                        if (!this._isMounted) {
                            this.setState({ users })
                        }
                        this.onLoadUser().then(() => {
                            this.countPosts(this.props.posts)
                            this.countFollowers(this.props.users, this.state.user)
                        })
                    })
            })
        this._isMounted = true;
    }

    componentDidUpdate = () => {
        if (this._isMounted && !this._isUpdated) {
            const urlId = this.props.match.params.id
            if (this.state.user._id !== urlId) {
                this.props.loadPosts()
                    .then((posts) => {
                        this.setState({ posts })
                        this.props.loadUsers()
                            .then((users) => {
                                this.setState({ users })
                                this.onLoadUser().then(() => {
                                    this.countPosts(this.props.posts)
                                    this.countFollowers(this.props.users, this.state.user)
                                })
                            })
                    })
            }
            this._isUpdated = true;
        }
    }

    componentWillUnmount = () => {
        // console.log('will unmount')
        // this.setState(prevState => {
        //     return {
        //         ...prevState,
        //         postsCount: 0,
        //         posts: this.props.posts,
        //         users: this.props.users,
        //         // user: {},
        //         followersCount: 0,
        //         followingCount: 0
        //     };
        // });
    }


    onLoadUser = async () => {
        const id = this.props.match.params.id
        await UserService.getById(id)
            .then((user) => {
                this.setState({ user: user })
            })
    }
    countPosts = (posts) => {
        let postsCount = 0
        posts.forEach((post) => {
            if (post.owner._id === this.state.user._id) {
                postsCount++
            }
        });
        let result = '';
        if (postsCount > 1) {
            result = postsCount + ' posts';
        } else if (postsCount === 0) { result = 'no posts' }
        else { result = '1 post'; }
        this.setState(prevState => {
            return {
                ...prevState,
                postsCount: result
            };
        });
    }
    countFollowers = (users, selectedUser) => {
        let followersCount = 0;
        let followingCount = 0;

        if (selectedUser.followers) {
            selectedUser.followers.forEach(() => {
                followersCount++
            })
        }

        users.forEach((user) => {
            if (user.following) {
                user.following.forEach((follower) => {
                    if (selectedUser._id === follower._id) {
                        followingCount++;
                    }
                })
            }
        });

        let followersResult = ''
        if (followersCount > 1) {
            followersResult = followersCount + ' followers';
        } else if (followersCount === 0) { followersResult = '0 followers' }
        else { followersResult = '1 follower'; }

        let followingResult = '';
        (followingCount > 0) ? followingResult = followingCount + ' following'
            : followingResult = '0 following'

        this.setState(prevState => {
            return {
                ...prevState,
                user: selectedUser,
                followersCount: followersResult,
                followingCount: followingResult
            };
        });
    }

    render() {
        const { user, postsCount, followersCount, followingCount } = this.state
        return (<div className="userpage-container">

            <div className="userpage-info flex col">
                <div className="user-image-username-container flex row">
                    <img className="userpage-user-img" alt="user pic" src={user.imgUrl}></img>
                    <div className="user-social-info-container flex col">
                        <span className="user-username">{user.username}</span>
                        <div className="user-social-info-section flex">
                            <span className="user-social-info-field">{postsCount}</span>
                            <span className="user-social-info-field">{followersCount}</span>
                            <span className="user-social-info-field">{followingCount} </span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        posts: state.post.posts,
        // friends: state.friend.friends,
        // selectedFriend: state.friend.selectedFriend,
        users: state.user.users,
        // user: state.user.user

    };
};
const mapDispatchToProps = {
    loadPosts,
    // loadFriends, loadSelectedFriend,
    loadUsers
};
export default connect(mapStateToProps, mapDispatchToProps)(UserPage);