import React, { Component } from 'react';
import '../assets/css/FriendPreview/FriendPreview.css'

export default class FriendPreview extends Component {
    onSelectFriend = (friend) => {
        if (this.props.onSelectFriend)
            this.props.onSelectFriend(friend);
    }
    render() {
        const { friend } = this.props;
        return <li key={friend._id} className="friend-item flex row" onMouseDown={() => this.onSelectFriend(friend)}>
            <div className="friend-wrapper flex row">
                <img className="friend-img" src={friend.imgUrl} alt="friend's pic" />
                <span className="friend-nickname">{friend.username}</span>
            </div>
        </li>
    }
}