import React from 'react';
import { connect } from 'react-redux';
import { loadPost, loadPosts, likeUnkikePost } from '../actions/PostActions.js';
const blank = require('../assets/img/like-blank.png')
const liked = require('../assets/img/like-active.png')

class Like extends React.Component {
    _isMounted = false;
    state = {
        isLiked: false,
        likeImgSrc: blank,
        post: this.props.post,
        user: this.props.user
    }
    async componentDidMount() {
        this._isMounted = true;
        if (this._isMounted) {
            const { post, user } = this.state
            this.checkLikeState(post, user)
        }
    }
    async componentDidUpdate(prevProps, prevState) {
        if (JSON.stringify(prevProps.post.likedBy) !== JSON.stringify(this.props.post.likedBy)) {
            this.checkLikeState(this.props.post, this.props.user)
        }
    }
    async componentWillUnmount() {
        this._isMounted = false;
    }

    onLikeClicked = async (post, user, isLiked) => {
        var newIsLiked = isLiked;
        var isDone = false
        if (isLiked) {
            newIsLiked = false
            this.setState({ isLiked: newIsLiked, likeImgSrc: blank })
            isDone = true
        }
        if (!isLiked) {
            newIsLiked = true
            this.setState({ isLiked: newIsLiked, likeImgSrc: liked })
            isDone = true
        }

        if (isDone) {
            try {
                await this.props.likeUnkikePost(post, user, isLiked)
            } catch (err) {
                console.log('error in likeUnkikePost: ', err)
            }
        }
    }
    checkLikeState = (post, user) => {
        if (user) {
            this.props.loadPost(post._id)
                .then((res) => {
                    var isFound = false;
                    res.likedBy.forEach(liker => {
                        if (liker._id === user._id) {
                            isFound = true;
                            this.setState({ isLiked: true, likeImgSrc: liked, post: res, user })
                        }
                    });
                    if (!isFound) {
                        this.setState({ isLiked: false, likeImgSrc: blank, post: res, user })
                    }
                })
        }
    }

    render() {
        const { btnClassName, post, user } = this.props
        const { isLiked, likeImgSrc } = this.state
        return (
            <img className={btnClassName} alt="like" title="like"
                onClick={() => this.onLikeClicked(post, user, isLiked)} src={likeImgSrc}>
            </img>
        );
    }
}
const mapStateToProps = state => {
    return {
        users: state.user.users,
        loggedInUser: state.user.loggedInUser,
        posts: state.post.posts
    };
};
const mapDispatchToProps = {
    loadPost, loadPosts, likeUnkikePost
};
export default connect(mapStateToProps, mapDispatchToProps)(Like);