import React, { Component } from 'react'
import ReactDOM from 'react-dom'
import '../assets/css/modals/ShareModal/ShareModal.css'

class ShareModal extends Component {

    componentDidMount() {
        this.modalTarget = document.createElement('div')
        this.modalTarget.className = "share-modal-" + this.props.postId + " share-modal-bg hidden";
        document.body.appendChild(this.modalTarget)
        this._render()
    }
    componentDidUpdate() {
        this._render()
    }
    componentWillUnmount() {
        ReactDOM.unmountComponentAtNode(this.modalTarget)
        document.body.removeChild(this.modalTarget)
    }
    _render() {
        const { postId } = this.props
        ReactDOM.render(
            <div className={`share-modal-div-${postId} share-modal-div`} >{this.props.children} </div>,
            this.modalTarget
        );
    }
    render() {
        return <noscript />;
    }
}
export default ShareModal