import React, { Component } from "react";
import '../assets/css/ScrollToTop/ScrollToTop.css';
const scrollToTopImg = require('../assets/img/scrollToTopImg.png');

export default class ScrollToTop extends Component {
    _isMounted = false;
    constructor(props) {
        super(props);
        this.state = {
            is_visible: false,
            isAppeared: false
        };
    }

    componentDidMount() {
        this._isMounted = true;
        if (this._isMounted) {
            window.addEventListener("scroll", this.toggleVisibility())
        }
    }
    componentWillUnmount() {
        this._isMounted = false;
        window.removeEventListener('scroll', this.toggleVisibility)
    }

    toggleVisibility() {
        var img = document.querySelector('.scroll-img')
        if (window.pageYOffset > 300) {
            this.setState({
                is_visible: true,
                isAppeared: true
            });
        }
        if (img !== null && this.state.isAppeared === true && window.pageYOffset > 300) {
            setTimeout(() => {
                this.setState({
                    is_visible: true
                });
            }, 700);
            img.classList.add('rotate-center-in');
        }
        else if (window.pageYOffset < 300 && this.state.isAppeared === true) {
            if (img !== null) {
                setTimeout(() => {
                    this.setState({
                        is_visible: false
                    });
                }, 700);
                img.classList.add('rotate-center-out');
            }
        }
    }

    scrollToTop() {
        window.scrollTo({
            top: 0,
            behavior: "smooth"
        });
    }

    render() {
        const { is_visible } = this.state;
        return (
            <div>
                {is_visible && (
                    <div onClick={() => this.scrollToTop()}>
                        <img className="scroll-img" alt="scrollToTop" src={scrollToTopImg}></img>
                    </div>
                )}
            </div>
        );
    }
}
