import React, { Component } from 'react';
import { connect } from 'react-redux';
import '../assets/css/LoggedinUserPage/loggedinUserPage.css'
import { loadPosts } from '../actions/PostActions';
import { loadUsers } from '../actions/UserActions';
import UserService from '.././services/UserService';

class LoggedInUserPage extends Component {
    state = {
        postsCount: 0,
        followersCount: 0,
        followingCount: 0,
        posts: [],
        users: [],
        user: {},
    }
    _isMounted = false;
    _isUpdated = false;

    componentDidMount() {
        this.props.loadPosts()
            .then((posts) => {
                if (!this._isMounted) {
                    this.setState({ posts })
                }
                this.props.loadUsers('LoggedInUserPage')
                    .then((users) => {
                        if (!this._isMounted) {
                            this.setState({ users })
                        }
                        this.onLoadUser().then(() => {
                            const postsResult = this.countPosts(this.props.posts)
                            this.countFollowers(postsResult)
                        })
                    })
            })
        this._isMounted = true;
    }
    componentDidUpdate = () => {
        if (this._isMounted && !this._isUpdated) {
            const urlId = this.props.match.params.id
            if (this.state.user._id !== urlId) {
                this.props.loadPosts()
                    .then((posts) => {
                        this.setState({ posts })
                        this.props.loadUsers()
                            .then((users) => {
                                this.setState({ users })
                                this.onLoadUser().then(() => {
                                    const postsResult = this.countPosts(this.props.posts)
                                    this.countFollowers(postsResult)
                                })
                            })
                    })
            }
            this._isUpdated = true;
        }
    }

    componentWillUnmount() {
        this._isMounted = false;
        this._isupdated = false;
    }

    onLoadUser = async () => {
        const id = this.props.match.params.id
        await UserService.getById(id)
            .then((user) => {
                this.setState({ user: user })
            })
    }
    countPosts = (posts) => {
        let postsCount = 0
        posts.forEach(post => {
            if (post.owner._id === this.state.user._id) {
                postsCount++
            }
        });
        return this.literalAmount(postsCount, 'post')
    }

    literalAmount = (count, type) => {
        let result = ''

        if (type === 'following') {
            if (count > 1) {
                result = count + ` ${type}`;
            } else if (count === 0) { result = `0 ${type}` }
            else { result = `1 ${type}`; }
            return result;
        }
        else {
            if (count > 1) {
                result = count + ` ${type}s`;
            } else if (count === 0) { result = `0 ${type}s` }
            else { result = `1 ${type}`; }
            return result;
        }
    }
    countFollowers = (postsResult) => {
        const selectedUser = this.props.loggedInUser

        let followersCount = 0;
        let followingCount = 0;

        //followers
        if (selectedUser.followers) {
            selectedUser.followers.forEach(() => {
                followersCount++
            })
        }
        // let followersResult = this.literalAmount(followersCount, 'follower')

        //following
        if (selectedUser.following) {
            this.props.users.forEach((user) => {
                if (user.following) {
                    user.following.forEach((followed) => {
                        if (selectedUser._id === followed._id) {
                            followingCount++;
                        }
                    })
                }
            });
        }
        // let followingResult = this.literalAmount(followingCount, 'following')

        this.setState(prevState => {
            return {
                ...prevState,
                user: selectedUser,
                postsCount: postsResult,
                followersCount: this.literalAmount(followersCount, 'follower'),
                followingCount: this.literalAmount(followingCount, 'following'),
            };
        });
    }

    render() {
        // const { loggedInUser } = this.props
        const { user, postsCount, followersCount, followingCount } = this.state
        return (<div className="loggedInUserpage-container">

            <div className="loggedInUser-info flex col">
                <div className="loggedInUser-image-username-container flex row">
                    <img className="loggedInUser-img" alt="user pic" src={user.imgUrl}></img>
                    <div className="loggedInUser-social-info-container flex col">
                        <span className="loggedInUser-username">{user.username}</span>
                        <div className="loggedInUser-social-info-section flex row">
                            <span className="loggedInUser-social-info-field">{postsCount}</span>
                            <span className="loggedInUser-social-info-field">{followersCount}</span>
                            <span className="loggedInUser-social-info-field">{followingCount}</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        loggedInUser: state.user.loggedInUser,
        posts: state.post.posts,
        users: state.user.users
    };
};
const mapDispatchToProps = {
    loadUsers, loadPosts
};

export default connect(mapStateToProps, mapDispatchToProps)(LoggedInUserPage);