import React, { Component } from 'react'
import ReactDOM from 'react-dom'
import '../assets/css/modals/PostOptionsModal/PostOptionsModal.css'

class PostOptionsModal extends Component {

    componentDidMount() {
        var postId = this.props.postId
        this.modalTarget = document.createElement('div')
        this.modalTarget.className = "options-modal-" + postId + " options-modal-bg hidden";

        document.body.appendChild(this.modalTarget)



        this._render()




    }
    componentDidUpdate() {
        this._render()
    }
    componentWillUnmount() {
        // this.modalTarget.removeEventListener("click", this.props.toggleModal.bind(this.props.postId));
        ReactDOM.unmountComponentAtNode(this.modalTarget)
        document.body.removeChild(this.modalTarget)

    }
    _render() {
        const { postId } = this.props

        ReactDOM.render(
            <div className={`options-modal-div-${postId} options-modal-div`} >{this.props.children} </div>,
            this.modalTarget
        );
    }
    render() {
        return <noscript />;
    }
}
export default PostOptionsModal