import React, { Component } from 'react';
import '../assets/css/TextInput/TextInput.css'

export default class Comments extends Component {
    state = { txt: "" };
    handleChange = (ev) => {
        const { name, value } = ev.target;
        if (name === 'txt') {
            const addCommentBtn = document.querySelector('.post-txt-input-submit-btn')
            if (value === '') {
                addCommentBtn.classList.remove('highligt');
            } else {
                addCommentBtn.classList.add('highligt');
            }
        }
        this.setState({ [name]: ev.target.value });
    };
    onAddMessage = async (ev) => {
        ev.preventDefault();
        if (!this.state.txt) return
        this.props.sendComment(this.state.txt)
        this.setState({ txt: '' })
    }

    render() {
        const { user } = this.props
        return (<div className="post-txt-input-wrapper flex col">
            {<form className="post-txt-input-form" onSubmit={this.onAddMessage}>
                <input className="post-txt-input" type="text" name="txt" value={this.state.txt} placeholder="Add a comment..."
                    onChange={(ev) => this.handleChange(ev)} autoComplete="off" />
                <img className="post-txt-user-image" alt="user page" src={user.imgUrl}></img>
                <div className="add-comment-container-absolute">
                    <span className="post-txt-input-submit-btn" onClick={this.onAddMessage}>Post</span>
                </div>
            </form>}
        </div>);
    }
}