export default {
    getPosts,
    getPost,
    likePost,
    unlikePost,
    add,
    remove
}
// function getPosts() {
//     return postsJson;
// }
async function getPosts() {
    try {
        const posts = await HttpService.get('post');
        return posts;
    }
    catch (err) {
        console.log(err)
    }
}
async function getPost(postId) {
    try {
        const post = await HttpService.get(`post/${postId}`);
        // console.log('post: ' + JSON.stringify(post))
        return post;
    }
    catch (err) {
        console.log(err)
    }
}
async function add(post) {
    try {
        const addedPost = await HttpService.post(`post`, post);
        return addedPost

    } catch (err) {
        console.log(err)
    }
}
async function remove(postId) {
    return await HttpService.delete(`post/${postId}`, '');
}
async function likePost(likedByObj, post) {
    const postId = post._id;
    try {
        const res = await HttpService.post(`post/like`,
            { likedByObj, postId }
        );
        return res;
    } catch (err) {
        console.log('likePost failed Error: ', err)
    }

}
async function unlikePost(unlikedByObj, post) {
    const postId = post._id;
    try {
        const res = await HttpService.post(`post/unlike`,
            { unlikedByObj, postId }
        );
        return res;

    } catch (err) {
        console.log('likePost failed Error: ', err)
    }
}