import React from 'react';
import history from './history';
import { Switch, Router, Route } from 'react-router-dom';
import '../src/assets/css/App/App.css';

// Pages
import Home from './pages/Home.js';
import Login from './pages/Login.js';
import About from './pages/About.js';
// import PostAdd from './cmps/PostAdd.jsx';
import PostPage from './pages/PostPage.js';
import LoggedInUserPage from './pages/LoggedInUserPage.js';
import UserPage from './pages/UserPage.js';

// Components
import Nav from './cmps/Nav'
// import Footer from './cmps/Footer'

function App() {
  return (
    <div className="App">
      <Router history={history}>
        <Nav />
        <Switch>
          <Route path="/" history={history} component={Home} key={1} exact />
          <Route path="/about/:id" component={About} key={2} exact />
          <Route path="/post/:id/comments" component={PostPage} key={3} exact />
          <Route path="/user/:id" component={UserPage} key={4} exact />
          <Route path="/user/logged/:id" component={LoggedInUserPage} key={5} exact />
          <Route path="/login" component={Login} key={6} exact />
        </Switch>
      </Router>
    </div>
  );
}
export default App;
